.SlideIn-enter {
  /* transform: translateY(-200vh); */
  opacity: 0;
}
.SlideIn-enter.SlideIn-enter-active {
  /* transform: translateY(0); */
  -webkit-transition: all 1.5s;
  transition: all 1.5s;
  opacity: 1;
}
.SlideIn-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}
.SlideIn-exit.SlideIn-exit-active {
  /* position: absolute; */
  /* transform: translateY(-400vh); */
  opacity: 0;
  -webkit-transition: all 1.5s;
  transition: all 1.5s;
}

.Navbar-shadow {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 75px;
    background-color: white; 
    box-shadow: 0px 8px 15px 0px rgba(0,0,0,0.5);
    z-index: 2;
}

.Navbar-no-shadow {
    display: flex;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    height: 105px;
    background-color: white;
    box-shadow: none;
    z-index: 2;
}

.NameLeft {
    font-family: 'Avenir';
    color: rgb(0, 94, 117);
    font-weight: 800;
    font-size: 25px;
    padding: 6px 12px;
    align-content: center;
    background: -webkit-radial-gradient(left, rgb(0, 94, 117), #04f1f1);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.Navbar-Right {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
}

.Navbar-Left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    margin-left: 200px;
}

@font-face {
    font-family: "Avenir";
    src: url("/static/media/Avenir Roman.b3b06989.otf") format("opentype");
}

@media (max-width: 812px) {
    .Navbar-no-shadow {
        display: flex;
        justify-content: center;
        position: fixed;
        width: 100%;
        height: 105px;
        background-color: white;
        box-shadow: none;
        z-index: 2;
    }
    
    .Navbar-Right {
